<template>
  <div class="home-container">
    <!-- 头部 -->
    <van-nav-bar title="头条" fixed />
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <ArticleInfo v-for="item in artlist" :key="item.id" :title="item.title" :author="item.aut_name"
          :cmt-count="item.comm_count" :time="item.pubdate" :cover="item.cover"></ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticleApiList } from '@/api/articleAPI'

import ArticleInfo from '@/components/article/ArticleInfo.vue'

export default {
  name: 'HomeVue',
  data () {
    return {
      // 页码
      page: 1,
      // 每页多少数值z
      limit: 10,
      // 文章数组
      artlist: [],
      loading: true,
      finished: false,
      isLoading: false

    }
  },
  methods: {
    // 封装获取数据的方法
    async initArticleList (isRefresh) {
      // 发起get请求
      const { data: res } = await getArticleApiList(this.page, this.limit)
      console.log(res)

      if (isRefresh) {
        this.artlist = [...res, ...this.artlist]
        this.isLoading = false
      } else {
        this.artlist = [...this.artlist, ...res]
        this.loading = false
      }

      if (res.length === 0) {
        this.finished = true
      }
    },
    onLoad () {
      this.page++
      this.initArticleList()
    },
    onRefresh () {
      this.page++
      this.initArticleList(true)
    }
  },
  components: {
    ArticleInfo
  },
  created () {
    this.initArticleList()
  }

}
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0px 50px 0px;
}

.van-nav-bar {
  background: #007bff;
}

/deep/.van-nav-bar__title {
  color: white
}
</style>
